.left-sidebar {
  width: 164px;
  box-shadow: 1px 1px 0 rgb(0, 0, 0, 0.2);
  transition: box-shadow ease-in-out 0.1s, transform ease-in-out 0.1s;
  transform: translateZ(0);
  box-sizing: border-box;
  font-size: 13px;
  height: auto;
  background-color: white;
  min-height: 100vh;
}
.nav-btn {
  background-color: inherit;
  width: 100%;
  border: none;
  padding: 0;
}
.side-nav {
  height: auto;
  max-width: 100%;
  position: sticky;
  margin: 50px 0px;
  padding: 20px 0px;
}
.side-nav-div {
  padding: 10px 0px;
}
.side-nav-links {
  text-decoration: none;
  color: #3a3a3a;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 10px;
  transition: 0.2s;
}
.side-nav-links:hover {
  color: black;
}
.image-globe {
  width: 3vw;
}
.active {
  font-weight: bolder;
  color: black;
  background-color: rgb(255, 255, 255);
  border-right: solid 3px #ef8236;
}
@media (max-width: 760px) {
  .left-sidebar {
    position: absolute;
    transform: translateX(-100%);
    z-index: 10;
  }
}
